@import 'mixins';

.willpage {
  .progress {
    margin-top: 105px;

    ul {
      @include flex_center_center;
      padding-top  : 47px;
      margin-bottom: 47px;

      li {
        @include flex_center_center;
        opacity: 0.5;

        &.active {
          opacity: 1;
        }

        &.circle {
          position: relative;

          img {
            width : 22px;
            height: 22px;
          }

          p {
            position : absolute;
            bottom   : -24px;
            font-size: 14px;
          }
        }

        &.line {
          img {
            width : 127px;
            height: 2px;
          }
        }
      }
    }
  }

  .header {
    h2 {
      font-size: 36px;
    }

    margin-bottom: 62px;
  }

  .forms {
    padding   : 18px 18px 60px;
    background: $peach_color;

    div {
      #basicForm {
        display  : grid;
        max-width: 820px;
        margin   : 0 auto;

        label {
          font-size    : 24px;
          font-weight  : 700;
          margin-bottom: 25px;
          margin-left  : 24px;
        }

        input,
        textarea {
          @include input_outset;

          margin-bottom: 57px;

          &:first-of-type {}
        }

        textarea {
          height        : 106px;
          padding-top   : 18px;
          padding-bottom: 18px;
        }

        button[type='submit'] {
          @include yellow_button;
          min-width: 137px;
          max-width: 137px;
          margin   : 40px auto 0;
          @include hover_active;
        }
      }
    }
  }

  .commits {
    padding-top   : 60px;
    padding-bottom: 60px;

    h2 {
      padding-bottom: 20px;
    }

    p {
      padding-bottom: 10px;

      span {
        font-weight: 600;
      }
    }

    button {
      @include yellow_button;
      margin: 40px auto 0;
      @include hover_active;
    }
  }
}